.container1 {
  padding: 20px;
}

.box1 {
  width: 300px;
  height: 200px;
  border: none;
  background-color: grey;
  float: left;
  margin: 30px;
  text-align: center;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 0px 0px 4px rgb(83, 80, 80);
  cursor: pointer;
}
.box1 i {
  font-size: 2rem;
  color: white;
}

.box1 b {
  margin-top: 5px;
  color: white;
}

.cardstyle {
  width: 200px;
  float: left;
}

.cardstyle button {
  margin: 2px;

  padding: 2px;
}

.classdetail {
  border: 2px solid blanchedalmond;
  margin: 5px;
  width: 30%;
  float: left;
  font-size: 1.2rem;
}
.icon {
  font-size: 1.5rem;
  margin-right: 1.5rem;
  color: red;
  cursor: pointer;
}
.edit {
  font-size: 1.5rem;
  cursor: pointer;
}
