.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Adjust height as needed */
}

.d-flex {
  display: flex;
}

.box {
  width: 300px; /* Adjust width as needed */
  height: 200px; /* Adjust height as needed */
  margin: 0 10px; /* Adjust spacing between boxes */
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  background-color: gray; /* Background color */
  border: 1px solid #ccc; /* Border for the boxes */
  font-size: 1.2rem;
  box-shadow: 0px 0px 4px rgb(83, 80, 80);
}
.logout {
  display: flex;
  justify-content: flex-end; /* Aligns button to the right within the container */
  margin-top: 20px; /* Adjust top margin as needed */
  margin-right: 20px; /* Adjust right margin as needed */
  margin-bottom: 20px;
  margin-left: 20px;
  background-color:#b5d4f3;
}

.btn {
  margin-left: 20px;
  padding: 8px 16px; /* Adjust padding as needed */
  font-size: 14px; /* Adjust font size as needed */
}

